import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import InputLabel from '@material-ui/core/InputLabel';
import MenuItem from '@material-ui/core/MenuItem';
import FormHelperText from '@material-ui/core/FormHelperText';
import FormControl from '@material-ui/core/FormControl';
import Select from '@material-ui/core/Select';
import { Box } from '@material-ui/core';

const useStyles = makeStyles(theme => ({
  formControl: {
    margin: theme.spacing(2),
    minWidth: 120,
  },
  selectEmpty: {
    marginTop: theme.spacing(4),
  },
}));

export default function ChainSelect() {
  const classes = useStyles();
  const [chain, setChain] = React.useState(localStorage.getItem('chain'));

  const handleChange = event => {
    window.localStorage.setItem('chain', event.target.value);
    setChain(event.target.value);
    window.location.reload();
  };

  return (
    <Box>
      
      <FormControl className={classes.formControl}>
        <InputLabel id="demo-simple-select-helper-label">Select Chain</InputLabel>
        <Select labelId="demo-simple-select-helper-label" id="chain-select-helper" value={chain} onChange={handleChange}>
          <MenuItem value={"ETH"}>ETH</MenuItem>
          <MenuItem value={"BSC"}>Binance Smart Chain</MenuItem>
        </Select>
        <FormHelperText>What chain are you using?</FormHelperText>
      </FormControl>
    
    </Box>
  );
}
